body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Droid Sans", "Ubuntu", "Roboto", "Oxygen",
    "Cantarell", "Fira Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  text-align: center;
  background-color: #282c34;
}

.App-body {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  color: white;
}


.myPhoto {
    border: 1px solid #ddd;
    border-radius: 4px;
    padding: 5px;
    margin: 20px;
    max-width: 35%;
    width: 500px;
    zoom: 0.6;
    float: left;
}
.myPhoto:hover{
    transition-duration: 2.5s;
    box-shadow: 0 0 50px 25px rgba(243, 219, 4, 0.767);
    border: 1px solid #ddd;
    cursor: pointer;
}
text{
    display: flex;
    font-size: 0.9em;
    text-shadow: 2px 2px rgba(57, 122, 112, 0.452);
}
.photos{
    background-color: #343a4046;
    padding: 25px;

}
.readMore{
    color: #007bff;
    font-size: 0.7em;
    display: inline-block;
    cursor: pointer;
}
.icons > a{
    font-size: 4em;
    margin: 25px;
}
.icons {
    float: center;
}
.photography {
	cursor: pointer;
	margin-bottom: 35%;
	border: 1px solid #ddd;
    border-radius: 4px;
}

.photographyVert {
	cursor: pointer;
	border: 1px solid #ddd;
    border-radius: 4px;
    width: 70%; 
    height: 70%;
}

.photographyText{
	padding-bottom: 5px;
    font-size: 0.6em;
    text-shadow: 2px 2px rgba(57, 122, 112, 0.452);
    font-style: italic;
}

.normal {
	-webkit-filter: invert(0%);
	        filter: invert(0%);
}

.inverted {
	-webkit-filter: invert(100%);
	        filter: invert(100%);
}


.projectIcons>div>a{
    font-size: 5em;
}
.projectIcons>div{
    display: inline-block;
    margin: 40px;
}
.weatherBox{
    float:center;
    font-size: 0.5em;
    margin: 15px;
}
table, th, td {
	margin-left: 25%;
	margin-right: 25%;
  	border: 1px solid black;
}
button {
	margin-right: 20px;
	margin-bottom: 20px;
}
table {
	width: 50%;
}
.articles>p{
	background-color: #343a4046;
	margin-left: 20%;
	margin-right: 20%;
    font-size: 0.8em;
    text-shadow: 2px 2px rgba(57, 122, 112, 0.452);
}
.userPanel>img {
	border-radius: 50%;
	height: 72px;
	margin-top: 5px;
	width: 72px;
}

.userPanel {
	background-color: #343a4046;
	max-width: 50%;
	margin-left: 30%;
	margin-right: 30%;
	font-size: 2vw;
}

.usertag {
	font-size: 3vw;
}
